import React, { Component } from 'react';
import { render } from 'react-dom';
import './style.css';

class App extends Component {
  constructor() {
    super();

    this.taskInput = React.createRef();
    this.taskInput1 = React.createRef();
    this.taskInput2 = React.createRef();
    this.taskInput3 = React.createRef();
    
    this.addMyClick = this.addMyClick.bind(this);
  }
  addMyClick() {
    document.write(this.taskInput.current.value);
    document.write("\n");
    document.write(this.taskInput1.current.value);
    document.write("\n");
    document.write(this.taskInput2.current.value);
    document.write("\n");
    document.write(this.taskInput3.current.value);
    this.taskInput.current.value = "";
  }

  render() {
    return (
      <div>
        <input ref ={this.taskInput}/>
        <input ref ={this.taskInput1}/>
        <input ref ={this.taskInput2}/>
        <input ref ={this.taskInput3}/>

        <button onClick={this.addMyClick}>Add</button>
      </div>
    );
  }
}

render(<App />, document.getElementById('root'));


